.property {
    align-items: start;
    border-bottom: 1px solid var(--silver-darken5);
    box-sizing: border-box;
    column-gap: 15px;
    cursor: pointer;
    display: grid;
    grid-template-columns: 1fr auto;
    height: 47px;
    padding: 0 20px;
}

.property:global(.open) {
    cursor: default;
    grid-template-columns: 1fr 513px auto;
    height: auto;
    padding: 0 20px 9px;
}

.label {
    align-items: center;
    display: flex;
    gap: 5px;
    height: 24px;
    margin: 12px 0;
}

.property.property:global(.disabled) .name {
    opacity: 0.3;
}

.action {
    margin: 12px 0;
}

.property:not(:global(.open)):hover .action {
    background: var(--shade-light);
}